<template>
    <div>
        <h2 class="text-center">Listado de Elementos</h2>
        <div class="text-center">
            <b-list-group>
                <b-list-group-item v-for="item in books" :key="item.id">
                    {{ item.description }}
                </b-list-group-item>
            </b-list-group>

        </div>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'ListElements',
    data() {
        return {
            books: []
        };
    },
    mounted() {
        
    },
    methods: {
        getBooks() {
            const path = `${process.env.VUE_APP_BASE_URL}books/`
            axios.get(path)
            .then(res => {
                this.books = res.data
            })
            .catch(err => {
                console.error(err); 
            })
        }
    },
    created() {
        this.getBooks()
    },
};
</script>

<style lang="css" scoped>

</style>


