<template>
    <div>
        <b-navbar type="dark" variant="dark" class="px-2">
            <b-navbar-nav>
            <b-navbar-brand href="#">
                <b-icon-tools></b-icon-tools> Django-Vue
            </b-navbar-brand>

            <router-link :to="{ name: 'ListBook' }" v-slot="{ navigate }" active-class="is-active">
                <b-nav-item @click="navigate" :active='$route.name =="ListBook"'>
                    Books
                </b-nav-item>
            </router-link>

            <router-link :to="{ name: 'ListElements' }" v-slot="{ navigate }">
                <b-nav-item @click="navigate"  :active='$route.name =="ListElements"'>
                    Elements
                </b-nav-item>                
            </router-link>           

            <!-- Navbar dropdowns -->
            <b-nav-item-dropdown text="Types" left>
                <b-dropdown-item href="#" v-for="type in types" :key="type.id">{{ type.description }}</b-dropdown-item>
            </b-nav-item-dropdown>
            
            <!-- Navbar dropdowns -->
            <b-nav-item-dropdown text="Categorys" left>
                <b-dropdown-item href="#" v-for="category in categorys" :key="category.id">{{ category.description }}</b-dropdown-item>
            </b-nav-item-dropdown>

            </b-navbar-nav>
        </b-navbar>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'NavBar',
    data() {
        return {
            types: [],
            categorys: []
        };
    },
    mounted() {
        
    },
    methods: {
        findTypes() {
            axios.get(`${process.env.VUE_APP_BASE_URL}types/`)
            .then(res => {
                this.types =res.data
            })
            .catch(err => {
                console.error(err); 
            })
        },
        findCategorys() {
            axios.get(`${process.env.VUE_APP_BASE_URL}categorys/`)
            .then(res => {
                this.categorys =res.data
            })
            .catch(err => {
                console.error(err); 
            })
        },
        
    },
    created() {
        this.findTypes()
        this.findCategorys()
    },
};
</script>

<style lang="css" scoped>

</style>


